/* eslint-disable no-restricted-globals */
// This file has the global service worker for edify, it needs some modification for each install as documented in the documentation site
import { registerRoute } from 'workbox-routing';
import { NetworkFirst, StaleWhileRevalidate } from 'workbox-strategies';
import { CacheableResponsePlugin } from 'workbox-cacheable-response';
import { ExpirationPlugin } from 'workbox-expiration';

const DS_ENDPOINTS = [
    'https://au-1a144ffb.datastore.squiz.cloud',
    'https://au-103caaad.datastore.squiz.cloud',
    'https://au-deca5cbb.datastore.squiz.cloud',
    'https://au-fef19d82.datastore.squiz.cloud',
];

// Set the service worker to skip waiting
self.addEventListener('install', () => {
    // The promise that skipWaiting() returns can be safely ignored.
    self.skipWaiting();
});

registerRoute(
    // Check to see if the request is a navigation to a new page
    ({ request, url }) =>
        request.mode === 'navigate' &&
        !url?.pathname?.includes('_admin') &&
        !url?.pathname?.includes('_performance') &&
        !url?.pathname?.includes('_nocache') &&
        !url?.pathname?.includes('_recache'),
    // Use a Network First caching strategy
    new NetworkFirst({
        // Put all cached files in a cache named 'pages'
        cacheName: 'pages',
        plugins: [
            // Ensure that only requests that result in a 200 status are cached
            new CacheableResponsePlugin({
                statuses: [0, 200],
            }),
        ],
    })
);

// Cache CSS, JS, and Web Worker requests with a Stale While Revalidate strategy
registerRoute(
    // Check to see if the request's destination is style for stylesheets, script for JavaScript, or worker for web worker
    ({ request, url }) =>
        (request.destination === 'style' ||
            request.destination === 'script' ||
            request.destination === 'worker') &&
        !url?.pathname?.includes('__lib'),
    // Use a Stale While Revalidate caching strategy
    new NetworkFirst({
        // Put all cached files in a cache named 'assets'
        cacheName: 'assets',
        plugins: [
            // Ensure that only requests that result in a 200 status are cached
            new CacheableResponsePlugin({
                statuses: [0, 200],
            }),
        ],
    })
);

// Ceche datastore requests with a Network First stratergy
registerRoute(
    ({ url }) =>
        // Change this to the correct data store endpoint when deployed
        DS_ENDPOINTS.includes(url.origin),
    // Use a network first (request from DS, then fallback to cache if there is no network)
    new NetworkFirst({
        cacheName: 'datastore-cache',
        plugins: [
            new CacheableResponsePlugin({
                statuses: [0, 200],
            }),
        ],
    })
);

// Cache images with a Cache First strategy
registerRoute(
    // Check to see if the request's destination is for an image (note that this won't work unless the media site has the same root as the service worker)
    ({ request }) => request.destination === '_media',
    // Use a Cache First caching strategy
    new StaleWhileRevalidate({
        // Put all cached files in a cache named 'images'
        cacheName: 'images',
        plugins: [
            // Ensure that only requests that result in a 200 status are cached
            new CacheableResponsePlugin({
                statuses: [0, 200],
            }),
            // Don't cache more than 50 items, and expire them after 30 days
            new ExpirationPlugin({
                maxEntries: 50,
                maxAgeSeconds: 60 * 60 * 24 * 30, // 30 Days
            }),
        ],
    })
);

// Skip waiting for updates
addEventListener('message', (event) => {
    if (event.data && event.data.type === 'SKIP_WAITING') {
        self.skipWaiting();
    }
});
